@font-face {
	font-family: 'Font Awesome 5 Regular';
	font-style: normal;
	font-weight: 400;
	src: url("../fonts/fontawesome-pro-regular-400.eot");
	src: url("../fonts/fontawesome-pro-regular-400.eot?#iefix") format("embedded-opentype"),
	url("../fonts/fontawesome-pro-regular-400.woff2") format("woff2"),
	url("../fonts/fontawesome-pro-regular-400.woff") format("woff"),
	url("../fonts/fontawesome-pro-regular-400.ttf") format("truetype"),
	url("../fonts/fontawesome-pro-regular-400.svg#fontawesome") format("svg");
}

.far {
	font-family: 'Font Awesome 5 Regular';
	font-weight: 400;
}
