@font-face {
    font-family: 'Font Awesome 5 Solid';
    font-style: normal;
    font-weight: 900;
    src: url('../fonts/fontawesome-pro-solid-900.eot');
    src: url('../fonts/fontawesome-pro-solid-900.eot?#iefix') format('embedded-opentype'),
    url('../fonts/fontawesome-pro-solid-900.woff2') format('woff2'),
    url('../fonts/fontawesome-pro-solid-900.woff') format('woff'),
    url('../fonts/fontawesome-pro-solid-900.ttf') format('truetype'),
    url('../fonts/fontawesome-pro-solid-900.svg#fontawesome') format('svg');
}

.fa,
.fas {
    font-family: 'Font Awesome 5 Solid';
    font-weight: 900;
}
